import React, { Component } from 'react';

class Prices extends Component {
    state = {
        currency: 'USD'
     }
    render() {
        const { } = this.props;
        return (
            <div>
                <ul className="list-group">
                    <li className="list-group-item">
                        Bitcoin rate for {}
                    </li>
                </ul>
            </div>
         );
    }
}

export default Prices;